select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  padding: 8px;
  border-radius: 8px;
  background-color: #fff;
}

.select-wrapper {
  position: relative;
}

.select-wrapper:after {
  font-family: FontAwesome;
  content: "\f107";
  font-size: 24px;
  position: absolute;
  top: 28px;
  right: 18px;
  color: #9aa0b9;
  pointer-events: none;
}

select::-ms-expand {
  display: none;
}

.form-group input:focus,
select:focus {
  border-radius: 8px;
  outline-style: none;
  border-color: #a9afc5;
}

nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  min-height: 9vh;
  background-color: #9aa0b9;
}

.nav-links {
  display: flex;
  list-style: none;
  justify-content: space-evenly;
  padding-left: 20px;
  width: 20%;
}

.link-item {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}

.chosen {
  border-bottom: 1px solid #fff;
}

.chosen:hover {
  cursor: pointer;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
}

.container {
  max-width: 100vw;
  margin: 0 auto;
  flex: 1 0 auto;
}

.flex-grid {
  display: flex;
  height: 90vh;
  padding: 2rem 4rem;
  font-weight: bold;
  font-size: 14px;
}

.left {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
  flex: 1 1;
}

.b-left {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  margin-right: 20px;
  border-radius: 6px;
  min-height: 450px;
  max-height: 100%;
  text-align: start;
  padding: 20px 30px 0px 30px;
  background-color: #fff;
  color: #3d4252;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.025),
    0 15px 40px rgba(166, 173, 201, 0.15);
}

.right {
  flex: 0 0 70%;
  border-radius: 6px;
  min-height: 450px;
  max-height: 100%;
  text-align: center;
  padding: 0 30px 20px 30px;
  background-color: #fff;
  overflow: auto;
  color: #3d4252;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.025),
    0 15px 40px rgba(166, 173, 201, 0.15);
}

.left-top {
  display: flex;
  flex: 0 1 100px;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  margin-bottom: 20px;
  border-radius: 6px;
  background-color: #fff;
  color: #3d4252;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.025),
    0 15px 40px rgba(166, 173, 201, 0.15);
}

.left-bottom {
  flex: 1 1;
  border-radius: 6px;
  text-align: start;
  padding: 20px 30px 0px 30px;
  background-color: #fff;
  color: #3d4252;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.025),
    0 15px 40px rgba(166, 173, 201, 0.15);
}

table {
  width: 100%;
  border-spacing: 0;
  table-layout: fixed;
}

th,
td {
  text-align: center;
  padding: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

tbody tr:nth-child(odd) {
  background-color: #e4e6f1;
}

i {
  cursor: pointer;
}

